/* Search field */

.search-menu {
  padding: 10px;
  margin: 10px;
}

.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.search-books-input-wrapper {
  flex: 1 1;
  background: #e9e;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 15px;
  margin: 15px;
  font-size: 1.25em;
  /*border: none;*/
  /*outline: none;*/
}

/* Cancel button on search field */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: searchfield-cancel-button;
}


/* Map Field */
.map-container {
  /* Will place a small map at the bottom for small screens */
  padding-left: 0px;
  padding-right: 0px;
  height: calc(100vh - 95px);
  right: 0;
  width: 100%;
  z-index: 999;
}

.map-style {
  /*height: calc(80vh);*/
  width: 100%;
  z-index: 1000;


}

@media (min-width: 768px){

  .map-container {
    /* On bigger screens gives map breathing space to the right */
    /*padding-right: 10px;*/
    position: fixed;
  }
}

